
html{
    /* scroll-snap-type: y mandatory;
    scroll-behavior: smooth; */
    font-family: 'Inter', sans-serif;
}

h1{
    font-size: 3rem;
    font-family: 'Inter', sans-serif;
}

h2{
    font-size: 2.5rem;
}

p{
    font-size: 1.25rem;
    font-weight: 400;
}


@media screen and (min-width:320px) and (max-width:480px) {
    .header{
        height: 150vh ;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
    }
    
    .hero-container{
        width: 100%;
        padding: 10rem;
        display: flex;
        flex-direction: column;
    }

    .header-hero-text{
        animation-name: headerAnim;
        animation-duration: 2s;
    }

    .header-hero-text h1{
        font-size: 4rem;
        word-wrap:normal;
        font-weight: 900;
        line-height: 90%;
        margin-top: -60vh;   
        /* text-align: center; */
    }

    .header-hero-image{
        translate: -15rem -10rem;
        transform: scale(.7);
        margin-top: -20%;
    }

    .header-effect{
        z-index: 0;
        animation: ScaleAnim;
        animation-duration: 2s;
        transform: scale(.7);
        position: absolute;
    }
    
    .header-robot{
        z-index: 1;
        animation: SlideAnim;
        animation-duration: 2s;
        transform: scale(.7);
        position: absolute;

    }
    
    .header-bomb{
        z-index:3;
        animation: SlideAnim;
        animation-duration: 3s;
        transform: scale(.7);
        position: absolute;
    }

    
}

@media screen and (min-width:640px){
    .header{
        /* scroll-snap-align: center;
        scroll-behavior:smooth; */
        height: 700px;
        width: 100vw;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
    }
    
    .hero-container{
        width: 1000px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: row;
        /* padding: 1rem; */
    }

    .header-hero-text h1{
        font-size: 6rem;
        font-weight: 900;
        line-height: 90%;
        /* margin-top: -25rem; */
        /* text-align: center; */
    }
    
    .header-hero-text p{
        margin-top: -3rem;
    }
    
    .header-hero-text, .header-hero-image{
        flex: 1;
        width: 30rem;
        height: 30rem;
        margin: auto;
        /* padding: 10px; */
       
    }
    
    .header-hero-text{
        animation-name: headerAnim;
        animation-duration: 2s;
    }
    
    .header-hero-image{
        translate: -9em -10em;
        transform: scale(.7);
        
    }
    
    /* Illustration Start */
    .header-effect{
        z-index: 0;
        animation: ScaleAnim;
        animation-duration: 2s;
        position: absolute;
        overflow: hidden;
    }
    
    .header-robot{
        z-index: 1;
        animation: SlideAnim;
        animation-duration: 2s;
        position: absolute;
        overflow: hidden;
    }
    
    .header-bomb{
        z-index:3;
        animation: SlideAnim;
        animation-duration: 3s;
        position: absolute;
        overflow: hidden;
    }
}



/* Illustration Animation Starts */
@keyframes headerAnim{
    0%{
        opacity: 0;
        translate: 0px 20px;
    }
    100%{
        opacity: 1;
        translate: 0px  0px;
    }
}

@keyframes SlideAnim{
    0%{
        translate: 60rem 0px;
    }

    5%{
        translate: 50rem 0px;
    }
    
    100%{
        translate: 0px 0px;
    }
}

@keyframes ScaleAnim{
    0%{
        transform: scale(0);
    }
    25%{
        transform: scale(.8rem);
    }
    100%{
        transform: scale(1rem);
    }
}
/* Illustration Animation End */


/* About */
@media screen and (min-width:320px) and (max-width:480px) {
    .about{
        display: flex;
        align-items: center;
        justify-content: center;
        height:130vh;
        margin-top: -50%;
        padding: 1rem;
    }
    
    .about-container{
        /* width: 100vw; */
        display: flex;
        flex-direction: column-reverse;
        z-index: 1;
    }

    .about-profile-picture, .about-profile-image{
        flex: 1;
        padding: 1rem;
        width:90%;
        border-radius: 100%;
    }
    
    .about-profile-description{
        flex:1;
        padding: 1rem;
        margin-top: -80px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    
    .about-profile-description-Sub{
        margin-top: -2rem;
    }
    
    .about-underline{
        background-image: linear-gradient(120deg, #ffd500 0%, #ff8605 100%);
        background-repeat: no-repeat;
        background-size: 100% 0.2em;
        background-position: 0 88%;
        transition: background-size 0.25s ease-in;
        &:hover {
            background-size: 100% 88%;
            
        }
    }
}

@media screen and (min-width:640px){
    .about{
        display: flex;
        align-items: center;
        justify-content: center;
        scroll-snap-align: center;
        margin-top: 10%;
        height:100%;
    }
    
    .about-container{
        width: 60rem;
        display: flex;
        flex-direction: row;
        z-index: 1;
    }    
    
    .about-profile-picture, .about-profile-image{
        flex: 1;
        padding: 1rem;
        border-radius: 100%;
    }
    
    .about-picture-background{
        background-color: #ffd500;
        width: 1000px;
        height: inherit;
        transform: translateX(-100%);
        z-index: -99999;
        position: inherit;
    }
    
    .about-profile-description{
        flex:1;
        padding: 20px;
        margin-top: -80px;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    
    .about-profile-description-Sub{
        margin-top: -20px;
    }
    
    .about-underline{
        background-image: linear-gradient(120deg, #ffd500 0%, #ff8605 100%);
        background-repeat: no-repeat;
        background-size: 100% 0.2em;
        background-position: 0 88%;
        transition: background-size 0.25s ease-in;
        &:hover {
            background-size: 100% 88%;
            
        }
    }
    
}

/* About Ends */

/* Works Starts */
.card-container{
    background-color: #ffd500;
    width: 22rem;
    height: 25rem;
    align-items: center;
    justify-content: center;
    /* margin-bottom: 20px; */
}

.card-container:hover{
    transform: scale(1.05);
    transition: all .2s ease-in-out;
    filter: drop-shadow(0 0 .25rem rgb(42, 42, 42));
}

.card-thumbnail{
    width: inherit;
    height: 12rem;
    margin-bottom: 10px;
}

.card-title{
    font-size: 1.5rem;
    font-weight: 750;
}

.card-caption{
    margin-bottom: 10%;
}

.card-title, .card-caption{
    margin-left: 5%;
}

@media screen and (min-width:320px) and (max-width:480px) {
    .works{
        display: flex;
        align-items: center;
        justify-content: space-around;
        flex-direction: column;
        margin-top: 10%;
        height: 100%;
    }

    .work-containers{
        display: flex;
        max-width: 80%;
