        flex-direction: column;
        padding: 2rem;
    }
}

@media screen and (min-width:640px) {
    .works{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        margin: 4rem 0;
        height: 40rem;
